        font-size: 1.5rem;
    }

    .info-content {
        font-size: 0.9rem;
    }

    .achievement-notification {
        padding: 1rem;
    }

    .achievement-icon {
        font-size: 2rem;
    }

    /* Content Display Mobile */
    #content-display {
        width: 95%;
        bottom: 8%;
    }

    #content-title h1 {
        font-size: 1.5rem;
        letter-spacing: 1px;
    }

    #content-text p {
        font-size: 0.9rem;
        line-height: 1.6;
        padding: 0.6rem 0.8rem;
    }

    #content-hint p {
        font-size: 0.8rem;
    }
}
