BAAgB;QAChB,sBAAsB;IAC1B;;IAEA;QACI,iBAAiB;IACrB;AACJ","sourcesContent":["/* ===== CYBERPUNK 3D RESUME - MAIN STYLES ===== */\n\n/* --- RESET & BASE --- */\n* {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\n:root {\n    /* Cyberpunk Color Palette */\n    --neon-pink: #FF00FF;\n    --neon-blue: #00FFFF;\n    --neon-purple: #9D00FF;\n    --electric-yellow: #FFFF00;\n    --metal-silver: #C0C0C0;\n    --dark-bg: #0a0a0a;\n    --dark-gray: #1a1a1a;\n    --light-gray: #2a2a2a;\n\n    /* Glitch Colors */\n    --glitch-color-1: #FF00FF;\n    --glitch-color-2: #00FFFF;\n    --glitch-color-3: #FFFF00;\n\n    /* UI */\n    --ui-bg: rgba(10, 10, 10, 0.85);\n    --ui-border: rgba(255, 0, 255, 0.3);\n    --ui-glow: 0 0 10px var(--neon-pink);\n\n    /* Typography */\n    --font-main: 'Courier New', monospace;\n    --font-title: 'Orbitron', sans-serif;\n}\n\nhtml, body {\n    width: 100%;\n    height: 100%;\n    overflow: hidden;\n    font-family: var(--font-main);\n    background-color: var(--dark-bg);\n    color: var(--neon-blue);\n}\n\n/* --- WEBGL CONTAINER --- */\n#webgl-container {\n    width: 100%;\n    height: 100%;\n    position: fixed;\n    top: 0;\n    left: 0;\n    z-index: 1;\n}\n\n#webgl-container canvas {\n    display: block;\n    width: 100%;\n    height: 100%;\n}\n\n/* --- LOADING SCREEN --- */\n#loading-screen {\n    position: fixed;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    background: linear-gradient(135deg, #0a0a0a 0%, #1a0a1a 100%);\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    z-index: 9999;\n    transition: opacity 0.5s ease-out;\n}\n\n#loading-screen.fade-out {\n    opacity: 0;\n    pointer-events: none;\n}\n\n.loading-content {\n    text-align: center;\n}\n\n.glitch-text {\n    font-size: 4rem;\n    font-weight: bold;\n    color: var(--neon-pink);\n    text-shadow:\n        0 0 10px var(--neon-pink),\n        0 0 20px var(--neon-pink),\n        0 0 30px var(--neon-pink);\n    position: relative;\n    animation: glitch-animation 2s infinite;\n    margin-bottom: 2rem;\n}\n\n@keyframes glitch-animation {\n    0% {\n        text-shadow:\n            0 0 10px var(--neon-pink),\n            0 0 20px var(--neon-pink);\n    }\n    25% {\n        text-shadow:\n            2px 2px 0 var(--neon-blue),\n            -2px -2px 0 var(--electric-yellow);\n    }\n    50% {\n        text-shadow:\n            0 0 10px var(--neon-pink),\n            0 0 20px var(--neon-pink);\n    }\n    75% {\n        text-shadow:\n            -2px -2px 0 var(--neon-blue),\n            2px 2px 0 var(--electric-yellow);\n    }\n    100% {\n        text-shadow:\n            0 0 10px var(--neon-pink),\n            0 0 20px var(--neon-pink);\n    }\n}\n\n.loading-bar-container {\n    width: 300px;\n    height: 4px;\n    background: var(--dark-gray);\n    border: 1px solid var(--neon-pink);\n    margin: 2rem auto;\n    position: relative;\n    overflow: hidden;\n}\n\n.loading-bar {\n    width: 0%;\n    height: 100%;\n    background: linear-gradient(90deg, var(--neon-pink), var(--neon-blue), var(--neon-pink));\n    background-size: 200% 100%;\n    animation: loading-gradient 1.5s ease infinite;\n    box-shadow: 0 0 10px var(--neon-pink);\n    transition: width 0.3s ease;\n}\n\n@keyframes loading-gradient {\n    0% { background-position: 0% 50%; }\n    50% { background-position: 100% 50%; }\n    100% { background-position: 0% 50%; }\n}\n\n.loading-text, .loading-percentage {\n    color: var(--neon-blue);\n    font-size: 0.9rem;\n    margin-top: 0.5rem;\n    text-transform: uppercase;\n    letter-spacing: 2px;\n}\n\n/* --- UI OVERLAY --- */\n#ui-overlay {\n    position: fixed;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    pointer-events: none;\n    z-index: 10;\n}\n\n#ui-overlay > * {\n    pointer-events: auto;\n}\n\n/* --- NAVIGATION MENU --- */\n#main-nav {\n    position: fixed;\n    top: 0;\n    left: 0;\n    width: 280px;\n    height: 100%;\n    background: var(--ui-bg);\n    backdrop-filter: blur(10px);\n    border-right: 2px solid var(--neon-pink);\n    box-shadow: 0 0 20px rgba(255, 0, 255, 0.3);\n    transform: translateX(0);\n    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);\n    padding: 2rem 0;\n}\n\n#main-nav.nav-hidden {\n    transform: translateX(-100%);\n}\n\n.nav-logo {\n    font-size: 1.5rem;\n    font-weight: bold;\n    color: var(--neon-pink);\n    text-align: center;\n    margin-bottom: 2rem;\n    text-shadow: 0 0 10px var(--neon-pink);\n}\n\n.nav-items {\n    list-style: none;\n}\n\n.nav-items li {\n    padding: 1rem 2rem;\n    cursor: pointer;\n    transition: all 0.3s ease;\n    border-left: 3px solid transparent;\n    display: flex;\n    align-items: center;\n    gap: 1rem;\n}\n\n.nav-items li:hover {\n    background: rgba(255, 0, 255, 0.1);\n    border-left-color: var(--neon-pink);\n    color: var(--neon-pink);\n    text-shadow: 0 0 5px var(--neon-pink);\n}\n\n.nav-items li.active {\n    background: rgba(255, 0, 255, 0.2);\n    border-left-color: var(--neon-blue);\n    color: var(--neon-blue);\n}\n\n.nav-icon {\n    font-size: 1.2rem;\n}\n\n/* --- MENU TOGGLE BUTTON --- */\n#menu-toggle {\n    position: fixed;\n    top: 2rem;\n    left: 2rem;\n    width: 50px;\n    height: 50px;\n    background: var(--ui-bg);\n    border: 2px solid var(--neon-pink);\n    border-radius: 5px;\n    cursor: pointer;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    gap: 5px;\n    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);\n    z-index: 100;\n}\n\n/* Move button when menu is open */\n#menu-toggle.active {\n    left: calc(280px + 1rem);\n}\n\n#menu-toggle:hover {\n    box-shadow: 0 0 15px var(--neon-pink);\n    transform: scale(1.05);\n}\n\n#menu-toggle span {\n    width: 25px;\n    height: 2px;\n    background: var(--neon-pink);\n    transition: all 0.3s ease;\n}\n\n#menu-toggle.active span:nth-child(1) {\n    transform: rotate(45deg) translate(5px, 5px);\n}\n\n#menu-toggle.active span:nth-child(2) {\n    opacity: 0;\n}\n\n#menu-toggle.active span:nth-child(3) {\n    transform: rotate(-45deg) translate(7px, -6px);\n}\n\n/* --- SETTINGS PANEL --- */\n#settings-panel {\n    position: fixed;\n    top: 2rem;\n    right: 2rem;\n    z-index: 100;\n}\n\n.icon-button {\n    width: 50px;\n    height: 50px;\n    background: var(--ui-bg);\n    border: 2px solid var(--neon-blue);\n    border-radius: 5px;\n    cursor: pointer;\n    font-size: 1.5rem;\n    transition: all 0.3s ease;\n}\n\n.icon-button:hover {\n    box-shadow: 0 0 15px var(--neon-blue);\n    transform: rotate(90deg);\n}\n\n#settings-content {\n    position: absolute;\n    top: 60px;\n    right: 0;\n    background: var(--ui-bg);\n    border: 2px solid var(--neon-blue);\n    border-radius: 5px;\n    padding: 1.5rem;\n    min-width: 250px;\n    backdrop-filter: blur(10px);\n    box-shadow: 0 0 20px rgba(0, 255, 255, 0.3);\n}\n\n#settings-content.hidden {\n    display: none;\n}\n\n.setting-item {\n    margin-bottom: 1rem;\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    gap: 1rem;\n}\n\n.setting-item label {\n    color: var(--neon-blue);\n    font-size: 0.9rem;\n}\n\n.setting-item select,\n.setting-item input[type=\"range\"] {\n    background: var(--dark-gray);\n    color: var(--neon-blue);\n    border: 1px solid var(--neon-blue);\n    padding: 0.3rem;\n    border-radius: 3px;\n}\n\n/* --- MINI MAP (REMOVED - User feedback: unclear purpose) --- */\n#mini-map {\n    display: none;\n}\n\n#mini-map-canvas {\n    display: none;\n}\n\n/* --- INSTRUCTIONS (REMOVED - User feedback: WASD/SPACE controls don't exist) --- */\n#instructions {\n    display: none;\n}\n\n#instructions p {\n    display: none;\n}\n\n/* --- SCENE INFO (DEPRECATED - Now integrated into content-display) --- */\n#scene-info {\n    display: none !important;\n}\n\n#scene-title {\n    display: none !important;\n}\n\n#scene-description {\n    display: none !important;\n}\n\n/* --- BLACK SCREEN TRANSITION (Cinematic Slide Effect) --- */\n#black-transition {\n    position: fixed;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    background: #000000;\n    opacity: 0;\n    pointer-events: none;\n    z-index: 999;\n    transition: opacity 0.6s ease-in-out;\n}\n\n#black-transition.active {\n    opacity: 1;\n}\n\n/* --- 3D CONTENT DISPLAY SYSTEM (Subtitle Style) --- */\n#content-display {\n    position: fixed;\n    bottom: 15%;\n    left: 50%;\n    transform: translateX(-50%);\n    width: 80%;\n    max-width: 900px;\n    text-align: center;\n    pointer-events: none;\n    z-index: 100;\n}\n\n.content-section {\n    opacity: 0;\n    transform: translateY(20px);\n    transition: opacity 0.8s ease-out, transform 0.8s ease-out;\n    margin-bottom: 1.5rem;\n}\n\n.content-section.show {\n    opacity: 1;\n    transform: translateY(0);\n}\n\n.content-section.fade-out {\n    opacity: 0;\n    transform: translateY(-20px);\n}\n\n#content-title h1 {\n    font-size: 2.5rem;\n    color: var(--neon-pink);\n    text-shadow:\n        0 0 10px var(--neon-pink),\n        0 0 20px var(--neon-pink),\n        0 0 30px rgba(255, 0, 255, 0.5);\n    margin: 0;\n    padding: 0;\n    font-weight: bold;\n    text-transform: uppercase;\n    letter-spacing: 2px;\n}\n\n#content-text p {\n    font-size: 1.1rem;\n    line-height: 1.8;\n    color: var(--neon-blue);\n    text-shadow:\n        0 0 5px var(--neon-blue),\n        0 0 10px rgba(0, 255, 255, 0.3);\n    margin: 0;\n    padding: 1rem 2rem;\n    background: rgba(0, 0, 0, 0.3);\n    backdrop-filter: blur(5px);\n    border-left: 3px solid var(--neon-blue);\n    border-right: 3px solid var(--neon-blue);\n    white-space: pre-wrap;\n}\n\n#content-hint {\n    position: absolute;\n    bottom: -3rem;\n    left: 50%;\n    transform: translateX(-50%);\n    opacity: 0;\n    transition: opacity 0.5s ease;\n}\n\n#content-hint.show {\n    opacity: 1;\n    animation: hint-bounce 2s ease-in-out infinite;\n}\n\n#content-hint p {\n    font-size: 0.9rem;\n    color: var(--electric-yellow);\n    text-shadow: 0 0 5px var(--electric-yellow);\n    margin: 0;\n    letter-spacing: 1px;\n}\n\n@keyframes hint-bounce {\n    0%, 100% {\n        transform: translateX(-50%) translateY(0);\n    }\n    50% {\n        transform: translateX(-50%) translateY(5px);\n    }\n}\n\n/* --- NUCLEAR BUTTON (UI Layer) --- */\n#nuclear-button-container {\n    position: fixed;\n    bottom: 3rem;\n    right: 3rem;\n    z-index: 200;\n    opacity: 0;\n    transition: opacity 0.5s ease;\n    pointer-events: none;\n}\n\n#nuclear-button-container.show {\n    opacity: 1;\n    pointer-events: auto;\n}\n\n.nuclear-button {\n    position: relative;\n    width: 120px;\n    height: 120px;\n    border: none;\n    background: none;\n    cursor: pointer;\n    padding: 0;\n    outline: none;\n    transform-style: preserve-3d;\n    transition: transform 0.1s ease;\n}\n\n.nuclear-button:hover {\n    transform: scale(1.05);\n}\n\n.nuclear-button:active {\n    transform: scale(0.95) translateY(5px);\n}\n\n/* Button base () */\n.button-base {\n    position: absolute;\n    width: 100%;\n    height: 100%;\n    background: radial-gradient(circle at 30% 30%, #2a2a2a, #0a0a0a);\n    border-radius: 50%;\n    box-shadow:\n        0 8px 0 #000000,\n        0 10px 20px rgba(0, 0, 0, 0.8),\n        inset 0 -5px 10px rgba(0, 0, 0, 0.5);\n    border: 3px solid #1a1a1a;\n}\n\n/* Button top () */\n.button-top {\n    position: absolute;\n    top: 10%;\n    left: 10%;\n    width: 80%;\n    height: 70%;\n    background: radial-gradient(circle at 30% 30%, #ff3333, #cc0000);\n    border-radius: 50%;\n    box-shadow:\n        0 5px 15px rgba(255, 0, 0, 0.5),\n        inset 0 -3px 8px rgba(0, 0, 0, 0.4),\n        inset 0 3px 8px rgba(255, 100, 100, 0.4);\n    animation: nuclear-pulse 2s ease-in-out infinite;\n    transition: transform 0.1s ease, box-shadow 0.1s ease;\n}\n\n.nuclear-button:active .button-top {\n    transform: translateY(5px);\n    box-shadow:\n        0 2px 8px rgba(255, 0, 0, 0.3),\n        inset 0 -2px 5px rgba(0, 0, 0, 0.4),\n        inset 0 2px 5px rgba(255, 100, 100, 0.4);\n}\n\n@keyframes nuclear-pulse {\n    0%, 100% {\n        box-shadow:\n            0 5px 15px rgba(255, 0, 0, 0.5),\n            0 0 20px rgba(255, 0, 0, 0.3),\n            inset 0 -3px 8px rgba(0, 0, 0, 0.4),\n            inset 0 3px 8px rgba(255, 100, 100, 0.4);\n    }\n    50% {\n        box-shadow:\n            0 5px 20px rgba(255, 0, 0, 0.8),\n            0 0 40px rgba(255, 0, 0, 0.6),\n            inset 0 -3px 8px rgba(0, 0, 0, 0.4),\n            inset 0 3px 8px rgba(255, 100, 100, 0.4);\n    }\n}\n\n/* Button label */\n.button-label {\n    position: absolute;\n    bottom: -30px;\n    left: 50%;\n    transform: translateX(-50%);\n    color: #ff0000;\n    font-family: var(--font-main);\n    font-size: 0.9rem;\n    font-weight: bold;\n    letter-spacing: 3px;\n    text-shadow:\n        0 0 5px rgba(255, 0, 0, 0.8),\n        0 0 10px rgba(255, 0, 0, 0.4);\n    white-space: nowrap;\n}\n\n/* Disabled button state */\n.nuclear-button.disabled {\n    cursor: not-allowed;\n    opacity: 0.5;\n}\n\n.nuclear-button.disabled:hover {\n    transform: none;\n}\n\n.nuclear-button.disabled:active {\n    transform: none;\n}\n\n.nuclear-button.disabled .button-top {\n    animation: none;\n    background: radial-gradient(circle at 30% 30%, #666666, #444444);\n    box-shadow:\n        0 5px 15px rgba(100, 100, 100, 0.3),\n        inset 0 -3px 8px rgba(0, 0, 0, 0.4),\n        inset 0 3px 8px rgba(150, 150, 150, 0.2);\n}\n\n.nuclear-button.disabled .button-label {\n    color: #666666;\n    text-shadow:\n        0 0 5px rgba(100, 100, 100, 0.5),\n        0 0 10px rgba(100, 100, 100, 0.2);\n}\n\n/* --- PERFORMANCE STATS --- */\n#performance-stats {\n    position: fixed;\n    top: 100px;\n    left: 2rem;\n    background: rgba(0, 0, 0, 0.8);\n    color: var(--neon-blue);\n    padding: 1rem;\n    font-size: 0.8rem;\n    font-family: monospace;\n    border: 1px solid var(--neon-blue);\n    border-radius: 3px;\n}\n\n/* --- UTILITY CLASSES --- */\n.hidden {\n    display: none !important;\n}\n\n.cyber-button {\n    background: var(--ui-bg);\n    color: var(--neon-pink);\n    border: 2px solid var(--neon-pink);\n    padding: 0.8rem 1.5rem;\n    cursor: pointer;\n    font-family: var(--font-main);\n    text-transform: uppercase;\n    letter-spacing: 2px;\n    transition: all 0.3s ease;\n}\n\n.cyber-button:hover {\n    background: var(--neon-pink);\n    color: var(--dark-bg);\n    box-shadow: 0 0 20px var(--neon-pink);\n}\n\n/* --- RESPONSIVE DESIGN --- */\n@media (max-width: 768px) {\n    #main-nav {\n        width: 100%;\n    }\n\n    .glitch-text {\n        font-size: 2.5rem;\n    }\n\n    #instructions {\n        font-size: 0.75rem;\n        padding: 0.8rem;\n    }\n\n    #mini-map {\n        width: 150px;\n        height: 150px;\n        bottom: 1rem;\n        right: 1rem;\n    }\n\n    #scene-title {\n        font-size: 2rem;\n    }\n\n    #scene-description {\n        font-size: 1rem;\n    }\n}\n\n@media (max-width: 480px) {\n    #menu-toggle,\n    .icon-button {\n        width: 40px;\n        height: 40px;\n    }\n\n    #mini-map {\n        display: none;\n    }\n\n    #instructions {\n        max-width: 200px;\n    }\n}\n\n/* --- ANIMATIONS --- */\n@keyframes pulse-glow {\n    0%, 100% {\n        box-shadow: 0 0 5px var(--neon-pink);\n    }\n    50% {\n        box-shadow: 0 0 20px var(--neon-pink);\n    }\n}\n\n@keyframes scan-line {\n    0% {\n        transform: translateY(-100%);\n    }\n    100% {\n        transform: translateY(100%);\n    }\n}\n\n/* Scan line effect */\n.scan-line {\n    position: absolute;\n    width: 100%;\n    height: 2px;\n    background: linear-gradient(transparent, var(--neon-blue), transparent);\n    animation: scan-line 3s linear infinite;\n    pointer-events: none;\n}\n\n/* --- INFO PANEL --- */\n.info-panel {\n    position: fixed;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%) scale(0.9);\n    width: 90%;\n    max-width: 600px;\n    max-height: 80vh;\n    background: var(--ui-bg);\n    border: 2px solid var(--neon-pink);\n    border-radius: 10px;\n    padding: 2rem;\n    backdrop-filter: blur(15px);\n    box-shadow: 0 0 30px rgba(255, 0, 255, 0.5);\n    opacity: 0;\n    pointer-events: none;\n    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);\n    z-index: 1000;\n    overflow-y: auto;\n}\n\n.info-panel.show {\n    opacity: 1;\n    pointer-events: auto;\n    transform: translate(-50%, -50%) scale(1);\n}\n\n.info-close {\n    position: absolute;\n    top: 1rem;\n    right: 1rem;\n    width: 30px;\n    height: 30px;\n    background: transparent;\n    border: 2px solid var(--neon-pink);\n    color: var(--neon-pink);\n    font-size: 1.2rem;\n    cursor: pointer;\n    border-radius: 50%;\n    transition: all 0.3s ease;\n}\n\n.info-close:hover {\n    background: var(--neon-pink);\n    color: var(--dark-bg);\n    transform: rotate(90deg);\n}\n\n.info-title {\n    font-size: 2rem;\n    color: var(--neon-pink);\n    text-shadow: 0 0 10px var(--neon-pink);\n    margin-bottom: 1rem;\n    text-transform: uppercase;\n}\n\n.info-content {\n    color: var(--neon-blue);\n    line-height: 1.8;\n    font-size: 1rem;\n}\n\n.info-content p {\n    margin-bottom: 1rem;\n}\n\n.info-content ul {\n    list-style: none;\n    padding-left: 0;\n}\n\n.info-content li {\n    padding: 0.5rem 0;\n    border-left: 3px solid var(--neon-blue);\n    padding-left: 1rem;\n    margin-bottom: 0.5rem;\n}\n\n/* --- ACHIEVEMENT SYSTEM --- */\n.achievement-notification {\n    position: fixed;\n    top: -150px;\n    right: 2rem;\n    width: 350px;\n    background: var(--ui-bg);\n    border: 2px solid var(--electric-yellow);\n    border-radius: 10px;\n    padding: 1.5rem;\n    backdrop-filter: blur(10px);\n    box-shadow: 0 0 30px rgba(255, 255, 0, 0.5);\n    display: flex;\n    align-items: center;\n    gap: 1rem;\n    z-index: 2000;\n    transition: top 0.5s cubic-bezier(0.4, 0, 0.2, 1);\n}\n\n.achievement-notification.show {\n    top: 2rem;\n    animation: achievement-pulse 0.5s ease-in-out;\n}\n\n@keyframes achievement-pulse {\n    0%, 100% {\n        transform: scale(1);\n    }\n    50% {\n        transform: scale(1.05);\n    }\n}\n\n.achievement-icon {\n    font-size: 3rem;\n    flex-shrink: 0;\n    animation: achievement-spin 0.6s ease-in-out;\n}\n\n@keyframes achievement-spin {\n    from {\n        transform: rotate(0deg) scale(0.5);\n        opacity: 0;\n    }\n    to {\n        transform: rotate(360deg) scale(1);\n        opacity: 1;\n    }\n}\n\n.achievement-content {\n    flex: 1;\n}\n\n.achievement-title {\n    color: var(--electric-yellow);\n    font-size: 0.9rem;\n    font-weight: bold;\n    text-transform: uppercase;\n    letter-spacing: 1px;\n    margin-bottom: 0.3rem;\n}\n\n.achievement-name {\n    color: var(--neon-pink);\n    font-size: 1.2rem;\n    font-weight: bold;\n    margin-bottom: 0.3rem;\n    text-shadow: 0 0 5px var(--neon-pink);\n}\n\n.achievement-desc {\n    color: var(--neon-blue);\n    font-size: 0.85rem;\n}\n\n/* Achievement Progress Bar (REMOVED - User feedback: \"0/12\" progress bar not intuitive) */\n#achievement-progress {\n    display: none;\n}\n\n#achievement-count {\n    color: var(--electric-yellow);\n    font-weight: bold;\n    font-size: 1rem;\n    display: block;\n    margin-bottom: 0.5rem;\n}\n\n.achievement-bar {\n    width: 100%;\n    height: 6px;\n    background: var(--dark-gray);\n    border-radius: 3px;\n    overflow: hidden;\n}\n\n.achievement-bar-fill {\n    height: 100%;\n    background: linear-gradient(90deg, var(--neon-pink), var(--electric-yellow));\n    transition: width 0.5s ease-out;\n    box-shadow: 0 0 10px var(--electric-yellow);\n}\n\n/* --- RESPONSIVE UPDATES --- */\n@media (max-width: 768px) {\n    .info-panel {\n        width: 95%;\n        padding: 1.5rem;\n    }\n\n    .achievement-notification {\n        width: 90%;\n        right: 5%;\n    }\n\n    #achievement-progress {\n        bottom: 1rem;\n    }\n\n    /* Content Display Responsive */\n    #content-display {\n        width: 90%;\n        bottom: 10%;\n    }\n\n    #content-title h1 {\n        font-size: 2rem;\n    }\n\n    #content-text p {\n        font-size: 1rem;\n        padding: 0.8rem 1rem;\n    }\n}\n\n@media (max-width: 480px) {\n    .info-title {\n        font-size: 1.5rem;\n    }\n\n    .info-content {\n        font-size: 0.9rem;\n    }\n\n    .achievement-notification {\n        padding: 1rem;\n    }\n\n    .achievement-icon {\n        font-size: 2rem;\n    }\n\n    /* Content Display Mobile */\n    #content-display {\n        width: 95%;\n        bottom: 8%;\n    }\n\n    #content-title h1 {\n        font-size: 1.5rem;\n        letter-spacing: 1px;\n    }\n\n    #content-text p {\n        font-size: 0.9rem;\n        line-height: 1.6;\n        padding: 0.6rem 0.8rem;\n    }\n\n    #content-hint p {\n        font-size: 0.8rem;\n    }\n}\n"],"sourceRoot":""}]);
